function replace(source, translated) {
  const tagRE = /\[(?<tag>.+?)\((?<params>.*)\)\]/;
  const dialogRE = /\[[Nn]ame="(?<name>.*)"\](?<text>.*)/;
  const decisionRE = /\[[dD]ecision\(options="(?<options>.*)"[,|, ]+values="(?<values>.*)"\)\]/;
  const blankRE = /\[(?<tag>.*)\]/;
  var counter = {};

  const lines = source.split(`\n`);
  for (var tag in translated.data) {
    if (translated.data[tag]) {
      counter[tag] = 0;
    }
  }

  return lines.map((line) => {
    var obj = {};
    if (line.match(tagRE)) {
      var { tag, params } = line.match(tagRE).groups;
      tag = tag.toUpperCase();
      if (tag === `DECISION`) {
        params = line.match(decisionRE).groups;
        for (let key in params) {
          params[key] = params[key].split(`;`).map((v) => v.trim());
        }
      } else {
        var temp = {};
        params = params.split(`,`);
        if (params.length > 0) {
          params.forEach((param) => {
            param = param.trim();
            if (param.split(`=`).length > 1) {
              var [key, value] = param.split(`=`);
              temp[key] = value.replace(/"/g, ``);
            }
          });
        }
        params = temp;
        if(tag === `PREDICATE`){
          params.references = params.references.split(';')
        }
      }
      obj = { tag: tag, params: params };
    } else {
      if (line.match(dialogRE)) {
        var { name, text } = line.match(dialogRE).groups;
        if (text.length > 1 && text[0] === " ") text = text.trim();
        obj = {
          tag: `DIALOG`,
          params: {
            name: name.toUpperCase(),
            text: text,
          },
        };
      } else {
        if (line.match(blankRE)) {
          tag = line.match(blankRE).groups.tag;
          tag = tag.toUpperCase();
          obj = { tag: tag };
        } else {
          if (line.search("//") && line.length > 1 && line[0] !== `[`) {
            obj = {
              tag: `DESCRIPTION`,
              params: {
                text: line,
              },
            };
          }
        }
      }
    }
    if (obj.tag in translated.data) {
      if (obj.params) {
        if(obj.tag === `DIALOG`){
          if(obj.params.text){
            obj.params = {
              ...obj.params,
              ...translated.data[obj.tag][counter[obj.tag]].params,
            };
            counter[obj.tag] += 1;
          }
        } else {
          obj.params = {
            ...obj.params,
            ...translated.data[obj.tag][counter[obj.tag]].params,
          };
          counter[obj.tag] += 1;
        }
      }
    }
    return obj;
  });
}
export default replace