import Axios from "axios";
import React from "react";
import styled from "styled-components";

const SelectorWindow = styled.div`
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
`;

const Background = styled.div`
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: absolute;
`;

const ContentBox = styled.div`
  width: 80%;
  background-color: white;
  margin: 1rem auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  position: relative;
`;

const Select = styled.select`
  width: 100%;
  margin: 1rem 0;
  padding: 0.25rem;
`;

const SelectButton = styled.button`
  background-color: #212121;
  color: white;
  border: solid 1px;
  margin: 1rem 0;
  padding: 0.25rem;
`;

const EPSelector = (props) => {
  const stories = props.story_table;
  const main = stories.filter((s) => s.type === `main`);
  const activity = stories.filter((s) => s.type === `activity`);
  const mini_activity = stories.filter((s) => s.type === `mini_activity`);
  const ep_id = (story_id) => {
    const { ep } = story_id.match(/main_(?<ep>[0-9])/).groups;
    return `EP ${ep} : `;
  };
  const select = (
    <Select value={props.value} onChange={props.selectEP}>
      <option value="">Select Episode</option>
      <optgroup label="Main Story">
        {main.map((ep) => (
          <option key={ep.id} value={ep.id}>
            {ep_id(ep.id)} {ep.name.zh_CN}{" "}
            {ep.name.en_US ? `(${ep.name.en_US})` : ``}
          </option>
        ))}
      </optgroup>
      <optgroup label="Activity Story">
        {activity.map((ep) => (
          <option key={ep.id} value={ep.id}>
            {ep.name.zh_CN} {ep.name.en_US ? `(${ep.name.en_US})` : ``}
          </option>
        ))}
      </optgroup>
      <optgroup label="Mini Activity Story">
        {mini_activity.map((ep) => (
          <option key={ep.id} value={ep.id}>
            {ep.name.zh_CN} {ep.name.en_US ? `(${ep.name.en_US})` : ``}
          </option>
        ))}
      </optgroup>
    </Select>
  );
  return select;
};

const SubEPSelector = (props) => {
  const ep = props.ep;
  const select = (
    <Select value={props.value} onChange={props.selectIndex}>
      <option value="">Select Story</option>
      {ep.story.map((story) => (
        <option key={`${ep.id}_${story.index}`} value={story.index}>
          {story.code ? `${story.code} : ` : ``} {story.name.zh_CN}{" "}
          {story.name.en_US ? `(${story.name.en_US})` : ``}
        </option>
      ))}
    </Select>
  );
  return select;
};

const LanguageSelector = (props) => {
  const choices = props.choices;
  const lang_name = {
    zh_CN: "Chinese",
    en_US: "English",
    ja_JP: "Japanese",
    ko_KR: "Korea",
    th_TH: "Thai",
  };
  const select = (
    <Select value={props.value} onChange={props.selectLanguage}>
      <option value="">Select Language</option>
      {choices.map((choice) => (
        <option key={choice.sort} value={choice.sort}>
          {lang_name[choice.language]}
          {` (${choice.publisher})`}
        </option>
      ))}
    </Select>
  );

  return select;
};

class StorySelector extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      ep: ``,
      index: ``,
      language: ``,
      publisher: ``,
      id: ``,
      choices: ``,
    };
  }

  selectEP = (event) => {
    this.setState({ ep: event.target.value, index: ``, id: `` });
  };
  selectIndex = (event) => {
    this.setState({ index: event.target.value }, async () => {
      if (event.target.value) {
        var data = await Axios.get(
          encodeURI(
            `${this.props.backend}/story/${this.state.ep}/${this.state.index}`
          )
        );
        if (data.data.length > 0) {
          this.setState({ choices: data.data });
        }
      }
    });
  };
  selectLanguage = (event) => {
    var choice = event.target.value;
    if (choice) {
      var story = this.state.choices.find((c) => c.sort === choice);
      this.setState({
        id: choice,
        language: story.language,
        publisher: story.publisher,
      });
    } else {
      this.setState({ id: `` });
    }
  };

  setSelect() {
    this.setState(
      {
        ep: this.props.selected.ep || "",
        index: this.props.selected.index || "",
        language: this.props.selected.language || "",
        publisher: this.props.selected.publisher || "",
      },
      async () => {
        if (this.state.ep && this.state.index) {
          var data = await Axios.get(
            encodeURI(
              `${this.props.backend}/story/${this.state.ep}/${this.state.index}`
            )
          );
          if (data.data.length > 0) {
            var choices = data.data;
            var id = `${this.state.ep}_${this.state.index}_${this.state.language}_${this.state.publisher}`;
            this.setState({ choices: choices, id: id });
          }
        }
      }
    );
  }

  componentDidMount() {
    this.setSelect();
  }
  componentDidUpdate(prevProps) {
    if (this.props.selected !== prevProps.selected) {
      this.setSelect();
    }
  }

  render() {
    return (
      <SelectorWindow>
        <Background onClick={() => this.props.close()} />
        <ContentBox>
          <EPSelector
            story_table={this.props.story_table}
            value={this.state.ep}
            selectEP={this.selectEP}
          />
          {this.state.ep ? (
            <SubEPSelector
              ep={this.props.story_table.find((ep) => ep.id === this.state.ep)}
              value={this.state.index}
              selectIndex={this.selectIndex}
            />
          ) : (
            <></>
          )}
          {this.state.choices && this.state.index ? (
            <LanguageSelector
              choices={this.state.choices}
              value={this.state.id}
              selectLanguage={this.selectLanguage}
            />
          ) : (
            <></>
          )}
          {this.state.id ? (
            <SelectButton
              onClick={() =>
                this.props.selectStory({
                  story_ep: this.state.ep,
                  story_index: this.state.index,
                  story_language: this.state.language,
                  id: this.state.id,
                  publisher: this.state.publisher,
                })
              }
            >
              Select
            </SelectButton>
          ) : (
            <></>
          )}
        </ContentBox>
      </SelectorWindow>
    );
  }
}

export default StorySelector;
