import React from "react";
import styled from "styled-components";
import SpriteHub from '../../lib/sprite_list.json'

const Background = styled.div`
  height: 100vh;
  width: 100vw;
  top:0;
  left:0;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.5);
  cursor:pointer;
`;
const CharImage = styled.img`
  width: auto;
  height: 80%;
`;

function CharacterImage(props) {
  var sprite = "";
  if (props.char.split("#").length === 1) {
    sprite = SpriteHub[props.char][1].sprite
  } else {
      var [char,i] = props.char.split("#")
    sprite = SpriteHub[char][i].sprite
  }
  return (
    <Background onClick={()=>props.close()}>
      <CharImage src={`./images/characters/${sprite}`}></CharImage>
    </Background>
  );
}

export default CharacterImage;
