import React from "react";
import styled, { css } from "styled-components";

const MainDisplay = styled.div`
  max-width: 60rem;
  margin: auto;
  background-color: #484848;
`;
const DescriptionBlock = styled.div`
  margin: 1rem;
  padding: 1rem;
  border: solid black;
  border-width: 0 8px 0 8px;
  color: #ffffff;
  background-color: #212121;
`;

const DialogBlock = styled.div`
  color: #ffffff;
  margin: 1rem 0;
`;

const DialogName = styled.div`
  padding: 1rem;
  background-color: #212121;
  font-weight: 700;
  width: fit-content;
  cursor: pointer;
  user-select: none;
`;
const DialogText = styled.div`
  padding: 0 1rem;
`;

const DecisionBlock = styled.div`
  display: flex;
  justify-content: space-around;
  margin: 1rem 0;
`;

const DecisionButton = styled.div`
  background-color: white;
  display: flex;
  padding: 1rem;
  margin: 0 1rem;
  align-items: center;
  cursor: pointer;
  user-select: none;
  ${(props) =>
    props.selected &&
    css`
      background-color: #212121;
      color: white;
    `}
`;

const BackgroundImage = styled.img`
  width: 100%;
  height: auto;
`;

const Description = (props) => {
  const lines = props.lines;
  const result = (
    <DescriptionBlock key={props.lines_nums}>
      {lines.map((line) => (
        <p key={line.index}>{line.text}</p>
      ))}
    </DescriptionBlock>
  );
  return result;
};

const Dialog = (props) => {
  const lines = props.lines;
  const result = (
    <DialogBlock key={props.lines_nums}>
      <DialogName onClick={() => props.selectChar(props.char)}>
        {props.name || `???`}
      </DialogName>
      <DialogText>
        {lines.map((line) => (
          <p key={line.index}>{line.text}</p>
        ))}
      </DialogText>
    </DialogBlock>
  );
  return result;
};

const Decision = (props) => {
  const line = props.line;
  const index = props.index;
  const result = (
    <DecisionBlock key={index}>
      {line.params.options.map((option, i) => (
        <DecisionButton
          key={`${props.index}_${line.params.values[i]}`}
          onClick={() =>
            props.select({ line: props.index, option: line.params.values[i] })
          }
          selected={props.selected === line.params.values[i]}
        >
          {option}
        </DecisionButton>
      ))}
    </DecisionBlock>
  );
  return result;
};

class Disply extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      decision: {},
      story: [],
    };
  }
  decide = (params) => {
    var decision = this.state.decision;
    decision[params.line] = params.option;
    this.setState({ decision: decision }, () => this.prepare());
  };
  selectChar = (char) => {
    this.props.selectChar(char);
  };
  prepare = () => {
    var result = [];
    var decision = this.state.decision;
    var background = "bg_black";
    var char_img = "";
    const story = this.props.story;
    for (var i = 0; i < story.length; i++) {
      var line = story[i];
      switch (line.tag) {
        case `BACKGROUND`:
          if (line.params?.image) {
            result.push(
              <BackgroundImage
                key={i}
                src={`./images/backgrounds/${line.params.image}.png`}
              ></BackgroundImage>
            );
          } else {
            result.push(
              <BackgroundImage
                key={i}
                src={`./images/backgrounds/bg_black.png`}
              ></BackgroundImage>
            );
          }
          background = line?.params?.image || "bg_black";
          break;
        case `IMAGE`:
          if (line.params?.image) {
            result.push(
              <BackgroundImage
                key={i}
                src={`./images/images/${line.params.image}.png`}
              ></BackgroundImage>
            );
          } else {
            result.push(
              <BackgroundImage
                key={i}
                src={`./images/backgrounds/${background}.png`}
              ></BackgroundImage>
            );
          }
          break;
        case `DIALOG`:
          if (line.params?.text) {
            var lines = [{ index: i, text: line.params.text }];
            var lines_nums = [i];
            var j = i + 1;
            while (
              story[j].tag === `DIALOG` &&
              story[j]?.params?.name === line.params.name
            ) {
              lines.push({ index: j, text: story[j].params.text });
              lines_nums.push(j);
              j += 1;
            }
            result.push(
              <Dialog
                key={i}
                lines={lines}
                name={line.params.name}
                lines_nums={lines_nums.join(`;`)}
                char={char_img}
                selectChar={(char) => this.selectChar(char)}
              ></Dialog>
            );
            i = j - 1;
          }
          break;
        case `DESCRIPTION`:
          lines = [{ index: i, text: line.params.text }];
          j = i + 1;
          var desc_id = [i];
          while (story[j].tag === `DESCRIPTION`) {
            lines.push({ index: j, text: story[j].params.text });
            desc_id.push(j);
            j += 1;
          }
          result.push(
            <Description
              key={i}
              lines={lines}
              lines_nums={desc_id.join(";")}
            ></Description>
          );
          i = j - 1;
          break;
        case `DECISION`:
          if (!decision[i]) {
            decision[i] = line.params.values[0];
          }
          decision.last = decision[i];
          result.push(
            <Decision
              key={i}
              line={line}
              index={i}
              selected={decision[i]}
              select={(params) => this.decide(params)}
            ></Decision>
          );
          break;
        case `PREDICATE`:
          var references = line.params.references;
          if (!references.includes(decision.last)) {
            var next = i + 1;
            while (
              story[next].tag !== `PREDICATE` &&
              !story[next].params?.references?.includes(decision.last)
            ) {
              next += 1;
            }
            i = next - 1;
          }
          break;
        case `CHARACTER`:
          if (line.params) {
            if (line.params.name2) {
              if (line.params.focus === "1") {
                char_img = line.params.name;
              } else {
                char_img = line.params.name2;
              }
            } else {
              char_img = line.params.name;
            }
          } else {
            char_img = "";
          }
          break;
        default:
          break;
      }
    }
    this.setState({ story: result, decision: decision });
  };

  componentDidMount() {
    if(this.props.story){
      this.prepare()
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.story !== prevProps.story) {
      this.prepare();
    } else {
      if (!this.state.story) {
        this.prepare();
      }
    }
  }
  render() {
    return (
      <MainDisplay>
        {this.state.story ? this.state.story.map((line) => line) : <></>}
      </MainDisplay>
    );
  }
}

export default Disply;
